import styled from "styled-components";
import theme from "Document.theme.js";
// import "react-select-plus/dist/react-select-plus.css";

export const SearchPlus = styled.div`
	display: flex;
	.Select-control {
		background-color: #fff;
		border-color: #d9d9d9 #ccc #b3b3b3;
		border-radius: 4px;
		border: 1px solid #ccc;
		color: #333;
		cursor: default;
		display: table;
		border-spacing: 0;
		border-collapse: separate;
		height: 36px;
		outline: none;
		overflow: hidden;
		position: relative;
		width: 100%;
	}
	.Select-placeholder,
	.Select--single > .Select-control .Select-value {
		bottom: 0;
		color: #aaa;
		left: 0;
		line-height: 34px;
		padding-left: 10px;
		padding-right: 10px;
		position: absolute;
		right: 0;
		top: 0;
		max-width: 100%;
		overflow: hidden;
		-o-text-overflow: ellipsis;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.Select.has-value.is-clearable.Select--single > .Select-control .Select-value {
		padding-right: 42px;
	}
	.Select.has-value.Select--single > .Select-control .Select-value .Select-value-label,
	.Select.has-value.is-pseudo-focused.Select--single > .Select-control .Select-value .Select-value-label {
		color: #333;
	}
	.Select-arrow {
		border-color: #999 transparent transparent;
		border-style: solid;
		border-width: 5px 5px 2.5px;
		display: inline-block;
		height: 0;
		width: 0;
		position: relative;
	}
	.Select-arrow-zone {
		cursor: pointer;
		display: table-cell;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 25px;
		padding-right: 5px;
	}
	.Select-clear-zone {
		/* -webkit-animation: Select-animation-fadeIn 200ms;
		animation: Select-animation-fadeIn 200ms; */
		color: #999;
		cursor: pointer;
		/* display: table-cell; */
		display: none;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 17px;
	}
	.Select-input {
		height: 34px;
		padding-left: 10px;
		padding-right: 10px;
		vertical-align: middle;
	}
	.Select-input > input {
		width: 100%;
		background: none transparent;
		border: 0 none;
		-webkit-box-shadow: none;
		box-shadow: none;
		cursor: default;
		display: inline-block;
		font-family: inherit;
		font-size: inherit;
		margin: 0;
		outline: none;
		line-height: 17px;
		padding: 8px 0 12px;
		-webkit-appearance: none;
	}

	.Select-menu-outer {
		background-color: $select-input-bg;
		border: 1px solid $select-input-border-color;
		border-top-color: mix($select-input-bg, $select-input-border-color, 50%);
		box-shadow: $select-menu-box-shadow;
		box-sizing: border-box;
		margin-top: -1px;
		max-height: $select-menu-max-height;
		position: absolute;
		top: 100%;
		width: 100%;
		z-index: $select-menu-zindex;
		-webkit-overflow-scrolling: touch;
	}

	.Select-menu {
		max-height: ($select-menu-max-height - 2px);
		overflow-y: auto;
	}

	.Select-option {
		box-sizing: border-box;
		background-color: $select-option-bg;
		color: $select-option-color;
		cursor: pointer;
		display: block;
		padding: $select-padding-vertical $select-padding-horizontal;

		&:last-child {
			@include border-bottom-radius($select-input-border-radius);
		}

		&.is-selected {
			background-color: $select-option-selected-bg;
			color: $select-option-selected-color;
		}

		&.is-focused {
			background-color: $select-option-focused-bg;
			color: $select-option-focused-color;
		}

		&.is-disabled {
			color: $select-option-disabled-color;
			cursor: default;
		}
	}

	.Select-noresults {
		box-sizing: border-box;
		color: $select-noresults-color;
		cursor: default;
		display: block;
		padding: $select-padding-vertical $select-padding-horizontal;
	}
`;



